  letter-spacing: 0.03em;
}
@media (max-width: 1199px) {
  .about h2 {
    margin: 0 17px 0 0;
    font-size: 42px;
    line-height: 50px;
  }
}
@media (max-width: 767px) {
  .about h2 {
    margin-bottom: 5px;
    font-size: 32px;
    line-height: 38px;
  }
}
.about h3 {
  display: block;
  margin: 0 0 39px;
  font-weight: 400;
  font-size: 18px;
  line-height: 27px;
}
@media (max-width: 1199px) {
  .about h3 {
    margin-bottom: 35px;
  }
}
@media (max-width: 767px) {
  .about h3 {
    margin-bottom: 24px;
  }
}
.about p {
