  }
}

.about__wrapper {
  position: relative;
  display: grid;
  grid-template-columns: 1fr 723px;
  margin-top: 120px;
  margin-bottom: 262px;
}
@media (max-width: 1199px) {
  .about__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 100px;
    margin-bottom: 204px;
  }
}
@media (max-width: 767px) {
  .about__wrapper {
    margin-top: 64px;
    margin-bottom: 88px;
    padding: 0;
  }
}

.about__text {
  padding-left: 60px;
  padding-right: 22px;
}
@media (max-width: 1199px) {
  .about__text {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0;
  }
}
@media (max-width: 767px) {
  .about__text {
    align-items: flex-start;
    padding: 0 15px;
  }
}

@media (max-width: 1199px) {
  .about__desktop {
    display: none;
  }
}

.about__wrapper-img {
  width: 660px;
  height: 401px;
  margin-left: 63px;
  overflow: hidden;
}
.about__wrapper-img img {
  object-fit: none;
  margin-top: -36px;
}
@media (max-width: 1199px) {
  .about__wrapper-img {
    width: 678px;
    height: 345px;
    margin: 0;
  }
  .about__wrapper-img img {
    max-width: none;
    width: 770px;
    height: 401px;
    object-fit: fill;
    margin-left: -92px;
    margin-top: -20px;
  }
}
@media (max-width: 767px) {
  .about__wrapper-img {
    width: 100%;
    height: 313px;
    margin-bottom: 40px;
  }
  .about__wrapper-img img {
    width: 100%;
    height: 313px;
    margin: 0;
    object-fit: cover;
    object-position: 90% center;
  }
}

.about__video {
  position: absolute;
  width: 364px;
  height: 228px;
  bottom: -115px;
  right: 399px;
  cursor: pointer;
}
.about__video iframe {
  width: 364px;
  height: 228px;
}
.about__video div {
  width: 100%;
  height: 100%;
}
.about__video img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  background-color: #ffffff;
}
.about__video button {
  position: absolute;
  width: 100px;
  height: 100px;
  top: calc(50% - 50px);
  left: calc(50% - 50px);
  padding: 0;
  box-sizing: border-box;
  border: none;
  background-color: transparent;
  z-index: 2;
}
.about__video svg {
  width: 100px;
  height: 100px;
}
.about__video::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(20, 54, 122, 0.5);
}
.about__video--close-cover::after {
  display: none;
}
.about__video:hover svg, .about__video:focus-within svg {
  opacity: 0.8;
}
@media (max-width: 1199px) {
  .about__video {
    width: 266px;
    height: 167px;
    bottom: -83px;
    right: calc(32% + 5px);
  }
  .about__video iframe {
    width: 266px;
    height: 167px;
  }
  .about__video button {
    width: 73px;
    height: 73px;
    top: calc(50% - 36px);
    left: calc(50% - 36px);
  }
  .about__video svg {
    width: 73px;
    height: 73px;
  }
}
@media (max-width: 767px) {
  .about__video {
    position: relative;
    width: 100%;
    height: auto;
    bottom: auto;
    right: auto;
    aspect-ratio: 320/167;
  }
  .about__video iframe {
    width: 100%;
    height: 100%;
  }
}

