.promo {
    h2 {
      margin: 4px 0 0 3px;
      padding-right: 52px;
      font-family: $ff-alumni-sans;
      font-weight: 600;
      font-size: 82px;
      line-height: 96%;
      letter-spacing: 0.03em;
      text-align: center;

      @include vp-1199 {
        font-size: 48px;
        margin-left: 6px;
        padding-right: 35px;
      }
    }

    dl {
      margin: 0;
      padding-right: 3px;

      @include vp-1199 {
        padding-right: 2px;
      }

      @include vp-767 {
        padding: 0;
      }
    }

    dt {
      margin: 0 0 3px;
      font-size: 18px;
      line-height: 150%;

      @include vp-1199 {
        font-size: 12px;
        margin-bottom: 1px;
      }
    }

    dd {
      display: inline-flex;
      vertical-align: middle;
      align-items: center;
      margin: 0 0 18px;
      font-weight: 700;
      font-size: 40px;
      line-height: 115%;
      border-top: 1px solid $color-default-white;

      @include vp-1199 {
        margin-bottom: 3px;
        font-size: 24px;
        line-height: 128%;
      }
    }

    span {
      padding-left: 7px;
      padding-top: 1px;
      font-weight: 400;
      font-size: 14px;
      line-height: 124%;

      @include vp-1199 {
        padding-left: 4px;
        font-size: 10px;
      }
    }

    p {
      margin: 0 0 45px;
      padding-left: 2px;
      font-size: 18px;
      line-height: 150%;

      @include vp-1199 {
        margin: -1px 1px 30px;
        padding: 0;
        font-size: 12px;
      }
    }
  }

  .promo__wrapper {
    display: grid;
    grid-template-columns: 1fr minmax(470px, 660px);
    margin-top: 120px;
    margin-bottom: 50px;

    @include vp-1199 {
      grid-template-columns: 1fr minmax(386px, 1fr);
      margin-top: 100px;
    }

    @include vp-767 {
      grid-template-columns: minmax(10px, 442px);
      justify-content: center;
      margin-top: 70px;
      overflow: hidden;
    }
  }

  .promo__content {
    display: flex;
    flex-direction: column;
    height: 500px;
    overflow-y: auto;
    padding: 82px 76px 85px;
    color: $color-default-white;
    background-color: $color-torch-red;

    @include vp-1199 {
      justify-content: space-between;
      height: auto;
      padding: 47px 44px 50px;
    }
  }

  .promo__date {
    display: flex;
    flex-wrap: wrap;

    @include vp-1199 {
      justify-content: space-between;
    }
  }

  .promo__btn {
    width: 100%;
    min-height: 71px;
    border: 4px solid $color-default-white;

    @include vp-1199 {
      font-size: 10px;
      min-height: 41px;
      border-width: 2px;
    }
  }

  .promo__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    background-color: $color-sapphire;

    @include vp-767 {
      width: 115%;
      height: 115%;
      object-position: 48% center;
      aspect-ratio: 290 / 292;
    }
  }
