.header {
    color: $color-default-white;
    background: $blue-to-red-top-desktop;

    @include vp-1199 {
        background: $blue-to-red-top-tablet;
    }

    @include vp-767 {
        background: $blue-to-red-top-mobile;
    }

    h3 {
        margin: 0 0 48px 17px;
        font-family: $ff-alumni-sans;
        font-weight: 600;
        font-size: 92px;
        line-height: 110px;
        letter-spacing: 0.05em;
        -webkit-text-stroke: 2px $color-default-white;
        text-transform: uppercase;

        @include vp-1199 {
            margin: 0 0 36px 25px;
            font-size: 72px;
            line-height: 86px;
            text-transform: none;
        }

        @include vp-767 {
            margin: 0 0 22px 0;
        }
    }

    h2 {
        margin: 43px 50px 14px 0;
        font-family: $ff-alumni-sans;
        font-weight: 600;
        font-size: 48px;
        line-height: 58px;
        letter-spacing: 0.03em;

        @include vp-1199 {
            margin: 28px 4px 7px 0;
            font-size: 34px;
            line-height: 41px;
        }

        @include vp-767 {
            margin: 29px 3px 14px 0;
            font-size: 28px;
            line-height: 34px;
        }
    }

    p {
        min-width: 113px;
        margin: 37px 0 0 19px;
        font-weight: 400;
        font-size: 14px;
        line-height: 21px;
        text-align: right;

        @include vp-1199 {
            margin: 30px 0 0;
        }

        @include vp-767 {
            margin: 22px 10px 0 0;
            min-width: 113px;
        }
    }
}

.header__wrapper {
    margin-top: 30px;
    margin-bottom: 51px;

    @include vp-1199 {
        margin-top: 35px;
        margin-bottom: 82px;
    }

    @include vp-767 {
        margin-top: 15px;
        margin-bottom: 44px;
    }
}

.header__wrapper-top {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    margin-bottom: 1px;

    @include vp-1199 {
        align-items: center;
        margin-bottom: 18px;
    }

    @include vp-767 {
        margin-bottom: 40px;
    }
}

.header__logo {
    line-height: 0;

    svg {
        width: 121px;
        height: 43px;
    }

    @include vp-1199 {
        svg {
            width: 81px;
            height: 17px;
        }
    }

    @include vp-767 {
        svg {
            width: 68px;
            height: 24px;
        }
    }
}

.header__phone {
    font-family: $ff-alumni-sans;
    font-weight: 600;
    font-size: 26px;
    line-height: 32px;
    letter-spacing: 0.03em;
    color: inherit;
    z-index: 1;

    @include vp-1199 {
        font-weight: 700;
        font-size: 24px;
        line-height: 29px;
        letter-spacing: 0.02em;
    }

    @include vp-767 {
        font-weight: 600;
        font-size: 20px;
        line-height: 24px;
    }

    @include hover-focus {
        opacity: 0.8;
    }
}

.header__content {
    display: grid;
    grid-template-columns: 0.5fr 1fr;
    align-items: center;

    @include vp-1199 {
        grid-template-columns: 1fr 1fr;
    }

    @include vp-767 {
        grid-template-columns: minmax(290px, 1fr);
        padding-bottom: 35px;
    }
}

.header__text {
    padding-right: 64px;
    text-align: right;

    @include vp-1199 {
        grid-column: auto;
        padding-right: 30px;
    }

    @include vp-767 {
        padding: 0;
    }
}

.header__title {
    display: grid;
    grid-template-columns: 2fr 1fr;
    align-items: center;
    justify-content: center;

    @include vp-767 {
        grid-template-columns: minmax(103px, 0.5fr) minmax(187px, 1fr);
    }
}

.header__btn {
    width: 233px;
    height: 61px;
    border: 4px solid $color-default-white;

    @include vp-1199 {
        width: 188px;
        margin-right: -2px;
    }

    @include vp-767 {
        margin-left: -1px;
        width: calc(100% + 4px);
        height: 62px;
    }
}

.header__img {
    box-sizing: border-box;
    max-height: 556px;
    aspect-ratio: 500 / 556;
    margin-top: -23px;

    @include vp-1199 {
        width: 100%;
        max-width: 324px;
        max-height: 390px;
        overflow: hidden;
        object-fit: cover;
        aspect-ratio: 324 / 390;
        margin: 0;
        grid-column: auto;
    }

    @include vp-767 {
        display: none;
    }
}
