.advantages {
    padding: 146px 0 91px;
    background-color: $color-ghost-white;

    @include vp-1199 {
      padding: 92px 0 95px;
    }

    @include vp-767 {
      padding: 65px 0 64px;
    }

    h2 {
      margin: 0 0 47px;
      padding-left: 12px;
      font-family: $ff-alumni-sans;
      font-weight: 600;
      font-size: 54px;
      line-height: 65px;
      letter-spacing: 0.03em;
      text-align: center;

      @include vp-1199 {
        margin-bottom: 45px;
        padding: 0 23px 0 0;
        font-size: 42px;
        line-height: 50px;
      }

      @include vp-767 {
        padding-left: 10px;
        padding-right: 27px;
        font-size: 32px;
        line-height: 38px;
      }
    }
  }

  .advantages__wrapper {
    @include vp-767 {
      padding: 0;
    }
  }

  .advantages__content {
    display: grid;
    grid-template-columns: 6fr 4.24fr 6fr;
    gap: 0 60px;
    padding: 0 60px;

    @include vp-1199 {
      grid-template-columns: repeat(2, 1fr);
      gap: 43px 30px;
      padding: 0;
    }

    @include vp-767 {
      grid-template-columns: 1fr;
      gap: 37px;
    }
  }

  .advantages__list {
    display: flex;
    align-items: flex-start;
    margin: 0 0 72px;
    padding-bottom: 75px;
    border-bottom: 2px solid transparentize($color-crimson, 0.8);

    @include vp-1199 {
      border: none;
      margin: 0;
      padding: 0;
    }

    @include vp-767 {
      flex-direction: column;
      align-items: center;
      padding: 0 46px;
    }


    &:last-child,
    &:nth-last-child(2) {
      border: none;
      margin: 0;
      padding-bottom: 0;
    }

    dt {
      margin: 0 31px 0 0;
      font-size: 14px;
      line-height: 150%;
      color: $color-persian-blue;

      @include vp-1199 {
        margin-right: 30px;
      }

      @include vp-767 {
        margin: 0 0 8px;
        text-align: center;
      }
    }

    span {
      display: block;
      font-weight: 700;
      font-size: 71px;
      line-height: 92%;
      background-clip: text;
      color: transparent;
      background-image: $red-to-blue;

      @include vp-1199 {
        font-size: 58px;
        line-height: 90%;
      }
    }

    dd {
      display: block;
      font-weight: 300;
      line-height: 140%;
      margin: 0;
      padding-right: 5px;

      @include vp-767 {
        padding: 0;
        text-align: center;
      }
    }

    p {
      margin: 0 0 8px;
      font-weight: 700;
      font-size: 22px;
      line-height: 150%;

      @include vp-1199 {
        margin-top: -3px;
        margin-bottom: 2px;
        font-size: 20px;
      }
    }

    &--center {
      height: 100%;
      grid-column: 2 / 3;
      grid-row: 1 / 3;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin: 0;
      padding-bottom: 0;
      color: $color-default-white;
      border: none;

      @include vp-1199 {
        grid-column: 1 / -1;
        grid-row: 2 / 3;
        padding: 0 118px;
      }

      @include vp-767 {
        grid-row: 3 / 4;
        padding: 0;
      }

      dt {
        display: block;
        width: 100%;
        margin: 0;
        padding: 57px 10px 32px;
        font-weight: 700;
        font-size: 26px;
        line-height: 150%;
        text-align: center;
        color: inherit;
        background-color: $color-resolution-blue;

        @include vp-1199 {
          margin: 0;
          padding: 37px 10px 13px;
          font-size: 22px;
        }

        @include vp-767 {
          margin: 6px 0 0;
          padding: 42px 10px 7px;
          line-height: 149%;
        }
      }

      span {
        display: block;
        padding-bottom: 5px;
        padding-right: 5px;
        font-size: 74px;
        text-align: center;
        color: inherit;
        background-color: $color-resolution-blue;
        -webkit-text-fill-color: initial;

        @include vp-1199 {
          padding-bottom: 4px;
          font-size: 74px;
        }

        @include vp-767 {
          padding-bottom: 0;
        }
      }

      dd {
        display: block;
        width: 100%;
        margin: 0;
        padding: 0 13px 57px;
        font-weight: 400;
        line-height: 150%;
        text-align: center;
        background-color: $color-resolution-blue;

        @include vp-1199 {
          padding: 0 113px 38px;
        }

        @include vp-767 {
          margin-bottom: 7px;
          padding: 0 52px 43px;
        }
      }
    }
  }
