.reviews {
    margin-bottom: 120px;

    @include vp-1199 {
      margin-bottom: 100px;
    }

    @include vp-767 {
      margin-bottom: 70px;
    }

    h2 {
      margin: 0 0 37px;
      padding-left: 9px;
      font-family: $ff-alumni-sans;
      font-weight: 600;
      font-size: 54px;
      line-height: 65px;
      text-align: center;
      letter-spacing: 0.03em;

      @include vp-1199 {
        margin-bottom: 40px;
        padding-left: 0;
        padding-right: 5px;
        font-size: 42px;
        line-height: 50px;
      }

      @include vp-767 {
        margin-bottom: 42px;
        padding-right: 3px;
        font-size: 32px;
        line-height: 38px;
      }
    }
  }

  .reviews__slider-wrapper {
    position: relative;
  }

  .reviews__slider {
    max-width: 560px;

    @include vp-1199 {
      max-width: 566px;
    }

    @include vp-767 {
      max-width: 226px;
    }
  }

  .reviews__swiper-wrapper {
    display: grid;
    gap: 30px;
  }


  .reviews__slide {
    display: grid;
    grid-template-columns: 80px 1fr;
    gap: 0 28px;
    padding: 54px 62px 56px;
    color: $color-default-white;
    background-color: $color-resolution-blue;

    @include vp-1199 {
      padding: 54px 65px 56px;
    }

    @include vp-767 {
      grid-template-columns: 84px 1fr;
      gap: 15px;
      padding: 40px 25px 33px;
    }

    picture {
      grid-row: span 2;

      @include vp-767 {
        grid-row: auto;
      }
    }

    img {
      width: 80px;
      height: 80px;
      margin-top: 8px;
      grid-row: span 2;

      @include vp-767 {
        width: 68px;
        height: 68px;
        grid-row: auto;
        margin-top: 0;
        margin-left: 16px;
      }
    }

    h3 {
      margin: 0 0 13px;
      font-weight: 700;
      font-size: 20px;
      line-height: 150%;

      @include vp-767 {
        margin-top: 8px;
        margin-bottom: 0;
        line-height: 130%;
      }
    }

    p {
      margin: 0;
      line-height: 140%;
      grid-column: 2 / 3;

      @include vp-767 {
        grid-column: 1 / -1;
        text-align: center;
      }
    }
  }

  .reviews__slider-button-prev,
  .reviews__slider-button-next {
    display: none;
    top: 52%;
    width: 18px;
    height: 34px;

    @include vp-767 {
      top: 53.5%;
      width: 12px;
      height: 21px;
    }

    &::after {
      color: $color-dark-grey;
      font-weight: 400;
      font-size: 32px;

      @include vp-767 {
        font-size: 20px;
      }
    }
  }

  .reviews__slider-button-prev {
    left: 242px;

    @include vp-1199 {
      left: -2px;
    }

    @include hover-focus {
      opacity: 0.8;
    }
  }

  .reviews__slider-button-next {
    right: 242px;

    @include vp-1199 {
      right: -2px;
    }

    @include hover-focus {
      opacity: 0.8;
    }
  }
