.coaches {
    position: relative;
    width: 100%;
    max-width: 1366px;
    margin: 106px auto 91px;
    padding: 0 103px;

    @include vp-1199 {
      margin-top: 101px;
      margin-bottom: 100px;
      padding: 0 101px;
    }

    @include vp-767 {
      margin-top: 64px;
      margin-bottom: 70px;
      padding: 0 47px;
    }

    h2 {
      margin: 0 0 48px 2px;
      font-family: $ff-alumni-sans;
      font-weight: 600;
      font-size: 54px;
      line-height: 65px;
      letter-spacing: 0.03em;
      color: $color-sapphire;

      @include vp-1199 {
        margin: 0 0 32px;
        padding-right: 6px;
        font-size: 42px;
        line-height: 50px;
        text-align: center;
      }

      @include vp-767 {
        font-size: 32px;
        line-height: 38px;
        margin-bottom: 42px;
      }
    }
  }

  .coaches__swiper {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
    gap: 40px 0;

    @include vp-1199 {
      grid-template-columns: repeat(auto-fit, minmax(283px, 1fr));
      gap: 30px 0;
    }

    @include vp-767 {
      grid-template-columns: repeat(auto-fit, minmax(226px, 1fr));
      gap: 30px;
    }
  }

  .coaches__slide {
    position: relative;
    width: 260px;
    height: 294px;
    margin: 0 auto;

    @include vp-1199 {
      width: 268px;
      height: 294px;
    }

    @include vp-767 {
      width: 226px;
      height: 274px;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      background-color: $color-grey;
    }
  }

  .coaches__slide-content {
    position: absolute;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    padding: 0 20px;
    color: $color-default-white;
    overflow-y: auto;

    h3 {
      margin: 0 0 22px;
      font-weight: 700;
      font-size: 18px;
      line-height: 150%;

      @include vp-1199 {
        margin-left: 8px;
      }
    }

    p {
      display: none;
      margin: 0 0 20px;
      line-height: 15px;
      color: $color-spindle;

      @include vp-1199 {
        margin-bottom: 19px;
        margin-right: 8px;
      }

      @include vp-767 {
        margin-bottom: 20px;
        margin-right: 0;
      }
    }

    ul {
      display: none;
      align-self: flex-start;
      margin: 18px 0 0 11px;
      list-style: none;

      @include vp-767 {
        margin-left: 0;
        padding-left: 30px;
      }
    }

    li {
      position: relative;
      margin-bottom: 13px;
      font-size: 14px;
      line-height: 150%;

      &::before {
        position: absolute;
        content: "\2022";
        top: 0;
        left: -17px;
        font-size: 24px;
      }
    }

    &:hover,
    &:focus-visible {
      justify-content: flex-start;
      background-color: transparentize($color-sapphire, 0.11);
      box-shadow: 0 0 35px rgba(42, 59, 99, 0.17);

      p,
      ul {
        display: block;
      }

      h3 {
        margin: 52px 0 0;

        @include vp-1199 {
          margin-right: 9px;
        }

        @include vp-767 {
          margin-top: 42px;
          margin-right: 0;
        }
      }
    }
  }

  .coaches__slider-button-prev,
  .coaches__slider-button-next {
    display: none;
    top: 65%;
    width: 18px;
    height: 34px;

    @include vp-1199 {
      top: 62%;
    }

    @include vp-767 {
      top: 65%;
      width: 12px;
      height: 21px;
    }

    &::after {
      color: $color-dark-grey;
      font-weight: 400;
      font-size: 32px;

      @include vp-767 {
        font-size: 20px;
      }
    }
  }

  .coaches__slider-button-prev {
    left: 45px;

    @include vp-1199 {
      left: 44px;
    }

    @include vp-767 {
      left: 14px;
    }

    @include hover-focus {
      opacity: 0.8;
    }
  }

  .coaches__slider-button-next {
    right: 45px;

    @include vp-1199 {
      right: 44px;
    }

    @include vp-767 {
      right: 14px;
    }

    @include hover-focus {
      opacity: 0.8;
    }
  }
